@import "./animations";

 :root {
  --background: #fff;
  --accent: #ddd;
  --primary: #2fa4d3;
  --primary: #E1490C;
  --radius: 12px; }

* {
  box-sizing: border-box; }

html, body, #root, .app {
  min-height: 100vh; }

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

.flex-align {
  align-items: center;
  display: flex; }

.box {
  align-items: center;
  border: solid 1px var(--accent);
  border-radius: var(--radius);
  display: flex;
  padding: 8px;

  &.column {
    align-items: flex-start;
    flex-direction: column; } }

button.add {
  align-items: center;
  background: none;
  border: dashed 2px var(--accent);
  border-radius: var(--radius);
  cursor: pointer;
  display: flex;
  color: #787878;
  font-size: inherit;
  justify-content: center;
  line-height: 1em;
  padding: 8px;
  transition: all .3s ease;

  &:hover {
    border-color: #ddd; }

  &::before {
    content: '+';
    margin-right: 8px; } }

.app {
  display: flex;
  flex-direction: column;

  > footer {
    align-items: center;
    display: flex;
    justify-content: center;
    margin-top: auto;
    padding: 8px;

    .love, strong {
      margin: 0 4px; }

    .love {
      animation: heartbit 2.5s ease infinite;
      color: #ababab;
      display: inline-flex;
      transition: all .3s ease; }

    &:hover .love {
      color: #d7443e; }

    a {
      color: inherit;
      text-decoration: none; } } }
